import React from 'react';
import '../css/JingXuan.css';
import "../font/iconfont.css"
import { Carousel,WingBlank } from 'antd-mobile';
import $ from "jquery"


class JingXuan extends React.Component {
  state = {
    list:["https://img.alicdn.com/tfs/TB1Oa.eSpXXXXb4XVXXXXXXXXXX-380-400.jpg",
    "https://img.alicdn.com/tfs/TB1RWs2afc3T1VjSZLeXXbZsVXa-380-400.jpg"],
    imgHeight: 176,
    list1:["1","2","3"],
    list3:[],
    list4:[]
  }

  componentDidMount(){
    var _this = this;
    $.ajax({
      type: "get",
      url: "https://vipshop.herokuapp.com/api/v1/product/byproducttype",
      data: {
        per:7,
        page:1,
        ptname:"猜你喜欢"
      },
      dataType: "json",
      success: function (data) {
        var arr = [];
        for(let i = 0; i < 3 ;i++){
          arr.push(data.info.products[i])
          _this.setState({list4:arr})
        }
        _this.setState({list3:data.info.products})
      }
    });
  }

  tap(id){
    this.props.history.push({pathname:"/detail/" + id})
    
  }
  
  render() {
    var _this = this
    return (
      <div className="souye">
          <WingBlank>
            <Carousel
              className="carousel"
              autoplay={true}
              infinite
            >
              {this.state.list.map((val,i) => (
                <a
                  key={i}
                  style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
                >
                  <img
                    src={val}
                    alt=""
                    className="img"
                    style={{ width: '100%'}}
                    onLoad={() => {
                      // fire window resize event to change height
                      window.dispatchEvent(new Event('resize'));
                      this.setState({ imgHeight: 'auto' });
                    }}
                  />
                </a>
              ))}
            </Carousel>
        </WingBlank>


        <div className="table">
          <figure>
            <figcaption className="iconfont icon-icon-test"></figcaption>
            <span>9.9特卖</span>
          </figure>
          <figure>
            <figcaption className="iconfont icon-pin"></figcaption>
            <span>特价拼团</span>
          </figure>
          <figure>
            <figcaption className="iconfont icon-tubiao-"></figcaption>
            <span>签到领福利</span>
          </figure>
          <figure>
            <figcaption className="iconfont icon-tuijian1"></figcaption>
            <span>好货推荐</span>
          </figure>
          <figure>
            <figcaption className="iconfont icon-miaosha"></figcaption>
            <span>超值秒杀</span>
          </figure>
        </div>


        <div className="Special">
          <WingBlank className="tejia">
            <h2>今日特价</h2>
              <Carousel
                className="carousel2"
                autoplay={false}
                infinite
              >
                {this.state.list1.map((val,i) => (
                  <a
                    key={val}
                    style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
                  >
                    <div className="cart">
                      {
                        this.state.list4.map(function(item,i){
                          return (
                            <div className="cart-item" key={i}>
                              <img src={item.imgs[1]}/>
                              <p>{item.description}</p>
                              <p>￥{item.style[0].price}</p>
                              <p>限时秒杀，先到先得</p>                      
                            </div>
                          )
                        })
                      }
                    </div>
                  </a>
                ))}
              </Carousel>
          </WingBlank>                   
        </div>


        <div className="soeyelist">
          {
            this.state.list3.map(function(item,i){
              return (
                <div class="goodscart-zy" key={i} onClick={_this.tap.bind(_this,item._id)}>
                  <img src={item.imgs[0]}/>
                  <p>{item.description}</p>
                  <p>￥{item.style[0].price}</p>
                  <butotn className="btn">去购买</butotn>
                </div>
              )
            })
          }
        </div>
      </div>
    );
  }
}


export default JingXuan;