import React,{Component} from "react";
import "../css/Address.css";
import Header from "./Header";
import Store from "../redux/Store";

class Address extends Component{   
    constructor(props){
        super(props)
        this.state={
            headname:"地址管理",
            address:[{name:"张三",tel:1586544267,addressdetail:"河南省郑州市二七区"},{name:"张三",tel:1586544267,addressdetail:"河南省郑州市二七区"}]
        }
        this.changeitem=this.changeitem.bind(this)
    }
    changeitem(){  
        this.setState({address:Store.getState()})
        // console.log(Store.getState())
    }
    componentDidMount(){     //利用监听函数，实现状态中的数据实时更新
        Store.subscribe(this.changeitem);   
    }
    
     
    add(){
        this.props.history.push('/add')
    }
    render(){       
        return(
            <div>
                <Header name={this.state.headname}/>
                {
                    this.state.address.map(function(item,i){
                        return(
                            <div key={i} className="address">
                                <span>{item.name}</span><span>{item.tel}</span>
                                <p>{item.addressdetail}</p>
                                <span className="iconfont icon-bianji"></span>
                            </div>
                        )
                    })
                }
                <p onClick={this.add.bind(this)} className="add">+新增收货地址</p>
            </div>
        )
    }
}

export default Address