import React,{Component} from "react";
import "../css/Feedback.css";
import Header from "./Header";


class Feedback extends Component{   
    constructor(props){
        super(props)
        this.state={
            headname:"功能反馈"
        }
    }
    save(){
        window.location.href = '#/person'
    }
    render(){       
        return(
            <div className="feeback">
                <Header name={this.state.headname}/>
                <p className="thanks">感谢您使用十元街APP,如果您对本软件的功能使用方面有任何建议和意见,欢迎告知，您的意见对我们非常重要!我们将不断优化改进,为您提供更好的用户体验</p>
                <textarea className="text" placeholder="请输入你的反馈意见"></textarea>
                <input type="text" placeholder="请输入你的手机号(选填)"/>
                <p className="save" onClick={this.save.bind(this)}>保存</p>
            </div>
        )
    }
}

export default Feedback