import React,{Component} from 'react';
import {HashRouter as Router,Route,NavLink,Redirect,Switch} from "react-router-dom"
import "../css/Souye.css"
import JingXuan from "../views/JingXuan"
import Dress from "../views/Dress"
import Department from "../views/Department"
import Food from "../views/Food"
import Wash from "../views/Wash"
import Shoes from "../views/Shoes"
import $ from "jquery"

class Souye extends Component{
    constructor(props){
        super(props)
        this.state = {
            list1:["精选","服饰内衣","母婴童装","食品酒水","美妆个护","鞋靴箱包","电脑数码","日用百货","家用电器","生鲜果蔬","时尚配饰","家居生活"],
            list2:["/index/souye/jx","/index/souye/dress","/index/souye/department","/index/souye/food","/index/souye/wash","/index/souye/shoes","/index/souye/Dress","/index/souye/Dress","/index/souye/Dress","/index/souye/Dress","/index/souye/Dress","/index/souye/Dress"]
        }
    }

    dian(){
        this.props.history.push('/search')
    }
    
       
    render(){
        var _this = this
        return (
            <div className="contain">
                <div className="top">
                    <h2>十元街</h2>
                    <input id="inp" type="text" onClick={this.dian.bind(this)}/>
                </div>

                <Router>
                    <div>
                    <div className="navBar">
                        <ul>
                            {
                                this.state.list1.map(function(item,i){
                                    return (
                                        <NavLink to={_this.state.list2[i]}><li key={i}>{item}</li></NavLink>
                                    )
                                })
                            }
                        </ul>
                        </div>
                        <Switch>
                            <Route path="/index/souye/jx" component={JingXuan}/>
                            <Route path="/index/souye/dress" component={Dress}/>
                            <Route path="/index/souye/department" component={Department}/>
                            <Route path="/index/souye/food" component={Food}/>
                            <Route path="/index/souye/wash" component={Wash}/>
                            <Route path="/index/souye/shoes" component={Shoes}/>
                            <Redirect from="/index/souye" to="/index/souye/jx"/>
                        </Switch>
                    </div>
                </Router>     
            </div>
        ) 
    }
}

export default Souye