import React,{Component} from "react";
import Header from "./Header";
import "../css/Compon.css";

class Set extends Component{   
    constructor(props){
        super(props)
        this.state={
            headname:"我的优惠券",
            list:[{money:1.2,name:"全品类(特例商品除外)",date:"2019.5.22-2019.5.30"},{money:1.5,name:"全品类(特例商品除外)",date:"2019.6.22-2019.7.30"}]
        }
    }
 
    render(){       
        return(
            <div className="compon">
                <Header name={this.state.headname}/>
                <ul className="use">
                    <li>未使用({this.state.list.length})</li>
                    <li>已使用(0)</li>
                    <li>已过期(0)</li>
                </ul>
                {
                    this.state.list.map(function(item,i){
                        return(
                            <div className="juan" key={i}>
                                <div className="money"><span>￥</span>{item.money}</div>
                                <div>
                                    <span className="jingjuan">京劵</span>
                                    <span className="quanpin">{item.name}</span>
                                    <p>{item.date}</p>
                                </div>
                            </div>
                        )
                       
                    })
                }
            </div>
        )
    }
}

export default Set