import React,{Component} from 'react';
import $ from "jquery"
import '../css/Dress.css';

class Dress extends Component{
    constructor(props){
        super(props)
        this.state = {
            list:[]
        }
    }

    componentDidMount(){
        var _this = this;
        $.ajax({
            type: "get",
            url: "https://vipshop.herokuapp.com/api/v1/product/byproducttype",
            data: {
                per:8,
                page:1,
                ptname:"女装"
            },
            dataType: "json",
            success: function (data) {
                _this.setState({list:data.info.products})
            }
        });
    }

    tap(id){
        this.props.history.push({pathname:"/detail/" + id})
        
      }

    render(){
        var _this = this;
        return (
            <div>
                <div className="fenlei">
                    <figure>
                        <figcaption><img src="https://gd2.alicdn.com/imgextra/i2/838034895/TB2zu1NGr1YBuNjSszeXXablFXa_!!838034895.jpg_80x80.jpg"/></figcaption>
                        <span>潮流女装</span>
                    </figure>
                    <figure>
                        <figcaption><img src="https://gw.alicdn.com/tfscom/img.alicdn.com/imgextra/i1/3341831677/O1CN01azDR9r1OG6iV8drpN_!!3341831677-0-beehive-scenes.jpg"/></figcaption>
                        <span>潮流男装</span>
                    </figure>
                    <figure>
                        <figcaption><img src="https://gw.alicdn.com/bao/uploaded/i3/2457309803/O1CN01JR8SYi2MHp7dGDIwA_!!2457309803.jpg_180x180xz.jpg_.webp"/></figcaption>
                        <span>内衣裤袜</span>
                    </figure>
                    <figure>
                        <figcaption><img src="https://img.alicdn.com/bao/uploaded/i1/TB123pHpOMnBKNjSZFCSut0KFXa.jpg"/></figcaption>
                        <span>运动户外</span>
                    </figure>
                </div>

                <div className="dress-list">
                    {   this.state.list.map((item,i) => {
                        return (
                            <div className="dress-item" key={i} onClick={_this.tap.bind(_this,item._id)}>
                                <img src={item.imgs[0]}/>
                                <p>{item.description}</p>
                                <span>￥{item.style[0].price}</span>
                                <button>去购买</button>
                            </div>
                        )
                    })
                        
                    }
                </div>
            </div>
        )
    }
}

export default Dress