import React,{Component} from "react";
import "../css/Add.css";
import Header from "./Header";
import Action from "../redux/Action";
import Store from "../redux/Store"

class Add extends Component{   
    
    constructor(props){
        super(props)
        this.state={
            headname:"新建收货人",
            list:{}
        }
    }
    
    address(){
        var obj={};
        var txt1=this.refs.txt1.value;
        var txt2=this.refs.txt2.value;
        var txt3=this.refs.txt3.value;
        obj.name=txt1;
        obj.tel=txt2;
        obj.addressdetail=txt3;
        Store.dispatch(Action.additem(obj));
        window.location.href = '#/address'
    }
    render(){       
        return(
            <div>
                <Header name={this.state.headname}/>
                <div className="add1">
                    <span>收货人：</span>
                    <input type="text" ref="txt1" placeholder="请填写收货人姓名"/>
                </div>
                <div className="add1">
                    <span>手机号码：</span>
                    <input type="text" ref="txt2" placeholder="请填写收货人电话"/>
                </div>
                <div className="add1">
                    <span>收件地址：</span>
                    <input type="text" ref="txt3" placeholder="请填写收货地址"/>
                </div>

                <p onClick={this.address.bind(this)} className="add2">保存</p>
                
            </div>
        )
    }
}

export default Add