import React,{Component} from 'react';
import $ from "jquery"
import '../css/Dress.css';

class Food extends Component{
    constructor(props){
        super(props)
        this.state = {
            list:[]
        }
    }

    componentDidMount(){
        var _this = this;
        $.ajax({
            type: "get",
            url: "https://vipshop.herokuapp.com/api/v1/product/byproducttype",
            data: {
                per:8,
                page:1,
                ptname:"美食"
            },
            dataType: "json",
            success: function (data) {
                _this.setState({list:data.info.products})
            }
        });
    }

    tap(id){
        this.props.history.push({pathname:"/detail/" + id})
        
      }

    render(){
        var _this = this
        return (
            <div>
                <div className="fenlei">
                    <figure>
                        <figcaption><img src="https://img12.360buyimg.com/n2/jfs/t1/4527/1/5981/186643/5ba1b24bE6c76c507/0a0ec4be7251df87.jpg!q80.webp"/></figcaption>
                        <span>坚果蜜饯</span>
                    </figure>
                    <figure>
                        <figcaption><img src="https://img12.360buyimg.com/n2/jfs/t15313/204/2594437802/143900/92f3e34c/5aaa3e06N77e00da6.jpg!q80.webp"/></figcaption>
                        <span>饼干糕点</span>
                    </figure>
                    <figure>
                        <figcaption><img src="https://img10.360buyimg.com/n2/jfs/t29533/131/735857141/115417/78bab8d1/5bfcc6b9N62622d9b.jpg!q80.webp"/></figcaption>
                        <span>酒水饮品</span>
                    </figure>
                    <figure>
                        <figcaption><img src="https://img11.360buyimg.com/n2/jfs/t16915/185/2028960439/242896/7b99353/5ae17780N19d696ae.jpg!q80.webp"/></figcaption>
                        <span>粮油调味</span>
                    </figure>
                </div>

                <div className="dress-list">
                    {   this.state.list.map((item,i) => {
                        return (
                            <div className="dress-item" key={i} onClick={_this.tap.bind(_this,item._id)}>
                                <img src={item.imgs[0]}/>
                                <p>{item.description}</p>
                                <span>￥{item.style[0].price}</span>
                                <button>去拼团</button>
                            </div>
                        )
                    })
                        
                    }
                </div>
            </div>
        )
    }
}

export default Food