import React,{Component} from "react";
import Header from "./Header";
import "../css/About.css";

class About extends Component{   
    constructor(props){
        super(props)
        this.state={
            headname:"关于"
        }
    }

    render(){       
        return(
            <div className="about">
                <Header name={this.state.headname}/>
                <img src="http://t.cn/E96QDEx"/>
                <p>iOS V3.0.2 build442</p>
                <div className="yinsi">
                    <span>隐私策略</span>
                    <span className="iconfont icon-zuojiankuohao"></span>
                </div>
                <p className="fenxiang">分享给小伙伴</p>
                <p className="copy">Copyright&copy;2018京东JD.com版权所有</p>
            </div>
        )
    }
}

export default About