import React,{Component} from "react"
import { SearchBar} from 'antd-mobile';
import $ from "jquery"
import "../css/SearchList.css"

class SearchList extends Component{
    constructor(props){
        super(props)
        this.state = {
            value:"",
            list:[]
        }
    }

    componentDidMount(){
        var _this = this;
        $.ajax({
            type: "get",
            url: "https://vipshop.herokuapp.com/api/v1/product/",
            data: {
                name:this.props.location.query?this.props.location.query.tit:"",
                per:7,
                pagr:1
            },
            dataType: "json",
            success: function (data) {
                _this.setState({list:data.info.products})
            }
        });
    }

    tap(){
        window.history.go(-1)
    }

    onChange= (value) => {
        this.setState({ value });
    };

    tap1(){
        var _this = this;
        $.ajax({
            type: "get",
            url: "https://vipshop.herokuapp.com/api/v1/product/",
            data: {
                name:this.state.value,
                per:7,
                pagr:1
            },
            dataType: "json",
            success: function (data) {
                _this.setState({list:data.info.products})
            }
        });
    }

    cancel(){
        window.history.go(-1)
    }

    tap2(id){
        this.props.history.push({pathname:"/detail/" + id})
        
      }

    render(){
        var _this = this;
        return (
            <div>
                <div className="searchlist-top">
                    <div id="inp">
                        <SearchBar placeholder="搜索商品" ref={ref => this.autoFocusInst = ref} 
                        onSubmit={this.tap1.bind(this)} value={this.state.value} onChange={this.onChange}
                        onCancel = {this.cancel.bind(this)}
                        />
                    </div>
                </div>

                <div className="searchlist-list">
                    {
                        this.state.list.map(function(item,i){
                            return (
                                <div class="goodscart-zy" key={i} onClick={_this.tap2.bind(_this,item._id)}>
                                    <img src={item.imgs[0]}/>
                                    <p>{item.description}</p>
                                    <p>￥{item.style[0].price}</p>
                                    <butotn className="btn">去购买</butotn>
                                </div>
                            )
                        })
                    }
                </div>
            </div>
        )
    }
}

export default SearchList