import React,{Component} from "react"
import { SearchBar,Icon} from 'antd-mobile';
import "../css/Search.css"
import $ from "jquery"

class Search extends Component{
    constructor(props){
        super(props)
        this.state = {
            value:"",
            seachhistory:["鞋子","连衣裙","沙滩短裤","鸭舌帽","七匹狼夹克","衬衣","拖鞋"]
        }
    }

    onChange= (value) => {
        this.setState({ value });
    };

    tap(){
        this.props.history.push({pathname:"/searchlist",query:{tit:this.state.value}})
    }

    detail(){
        var arr = [];
        this.setState({seachhistory:arr})
    }

    seach(i){
        this.setState({value:this.state.seachhistory[i]})

    }

    cancel(){
        window.history.go(-1)
    }

    render(){
        var _this = this;
        return (
            <div>
                    <SearchBar placeholder="搜索商品" ref={ref => this.autoFocusInst = ref} 
                    onSubmit={this.tap.bind(this)} value={this.state.value} onChange={this.onChange}
                    onCancel = {this.cancel.bind(this)}
                    />

                <div className="seachhistory">
                    <div className="hitory-top">
                        <span>搜索历史</span>
                        <span onClick={this.detail.bind(this)}><Icon type="cross-circle-o" size="xs"/></span>
                    </div>
                    <div className="historylist">
                        {
                            this.state.seachhistory.map(function(item,i){
                                return (
                                    <span key={i} onClick={_this.seach.bind(_this,i)} ref="txt1">{item}</span>
                                )
                            })
                        }
                    </div>

                </div>

            </div>
        )
    }
}

export default Search