import React,{Component} from "react";
import { Modal, Button, WhiteSpace, WingBlank, Toast } from 'antd-mobile';
import "../font/iconfont.css";
import "../css/Set.css";
import Header from "./Header";
const alert = Modal.alert;
const showAlert = () => {
    const alertInstance = alert('Delete', 'Are you sure???', [
      { text: 'Cancel', onPress: () => console.log('cancel'), style: 'default' },
      { text: 'OK', onPress: () => console.log('ok') },
    ]);
    setTimeout(() => {
      // 可以调用close方法以在外部close
      console.log('auto close');
      alertInstance.close();
    }, 500000);
  };

class Set extends Component{   
    constructor(props){
        super(props)
        this.state={
            headname:"设置"
        }
    }

    about(){
        this.props.history.push('/about')
    }
    tuichu(){
        sessionStorage.removeItem("token")
        this.props.history.push('/index/person1')
    }
    render(){       
        return(
            <div>
                <Header name={this.state.headname}/>
                <ul className="list1">
                    <li>
                        <p onClick={showAlert}>清除本地缓存</p>
                        <span>1.2M</span>
                        <span className="iconfont icon-zuojiankuohao"></span>
                    </li>
                    <li>
                        <p>设置登录密码</p>
                        <span></span>
                        <span className="iconfont icon-zuojiankuohao"></span>
                    </li>
                    <li onClick={this.about.bind(this)}>
                        <p>关于</p>
                        <span>V3.0.2</span>
                        <span className="iconfont icon-zuojiankuohao"></span>
                    </li>
                </ul>
                <div className="tuichu" onClick={this.tuichu.bind(this)}>退出登录</div>
            </div>
        )
    }
}

export default Set