import React,{Component} from "react";
import Header from "./Header";
import "../css/Confirm.css";
import $ from "jquery"
import Action from "../redux/Action";
import Store from "../redux/Store"

class Confirm extends Component{   
    constructor(props){
        super(props)
        this.state={
            id:this.props.match.params.id?this.props.match.params.id : "",
            headname:"确认订单",
            list:[],
            list1:[],
            style:"",
            min:1,
            num:""
        }
    }   

    tap(){
        var obj = {}
        var id = this.state.id
        obj[id] = this.refs.numm.value
        Store.dispatch(Action.addid(obj))
        this.props.history.push("/cart")
    }

    add(){
        this.refs.numm.value++
        this.refs.total.innerHTML = "￥"+this.Totalprice()
    }

    jian(){
        this.refs.numm.value--
        if(this.refs.numm.value <= 0){
            this.refs.numm.value=1
            alert("至少一件")
        }
        this.refs.total.innerHTML = "￥"+this.Totalprice()
    }

    Totalprice(){
        var totalprice = Math.round(this.refs.numm.value * this.state.style -5)
        return totalprice
    }

    componentDidMount(){  
        var _this = this
        $.ajax({
            type: "get",
            url: "https://vipshop.herokuapp.com/api/v1/product/" + this.state.id,
            dataType: "json",
            success: function (data) {
                _this.setState({list:data.info})
                _this.setState({list1:data.info.imgs})
                _this.setState({style:data.info.style[0].price})
            }
        });
    }

    render(){       
        return(
            <div className="confirm">
               <Header name={this.state.headname}/>
                <div className="address1">
                    <span>姓名</span><span>15677886430</span>
                    <p>河南郑州二七区</p>
                </div>
                <div className="store">
                    <div>
                        <img src={this.state.list1[0]}/>
                        <div className="gooddetail">
                            <p>{this.state.list.description}</p>
                            <div><span>￥{this.state.style}</span></div>
                            <p>支持7天无理由退换货</p>
                        </div>
                    </div>
                    <div className="num1">
                        <span>已选：</span>
                        <span onClick={this.add.bind(this)}>+</span>
                        <input type="text" className="numipt" min={this.state.min} ref="numm" value="1"/>
                        <span onClick={this.jian.bind(this)}>—</span>
                    </div>
                    <div className="peisong">
                        <span>配送</span><span>快递运输</span>
                    </div>
                </div>

                <div className="fapiao">
                    <div><span>发票</span><span>不开发票</span><span className="iconfont icon-zuojiankuohao"></span></div>
                    <div><span>优惠券</span><span>选择优惠券</span><span className="iconfont icon-zuojiankuohao"></span></div>
                </div>
                <div className="Settlement">
                    <div><span>商品金额</span><span>￥{this.state.style}</span></div>
                    <div><span>运费</span><span>+10.00</span></div>
                    <div><span>立减</span><span>-15.00</span></div>
                </div>
                <div className="submit">
                    <div><span ref="total">{this.state.style - 5}</span></div>
                    <p onClick={this.tap.bind(this)}>提交订单</p>
                </div>
            </div>
        )
    }
}

export default Confirm