import React,{Component} from 'react';
import $ from "jquery"
import '../css/Dress.css';

class Shoes extends Component{
    constructor(props){
        super(props)
        this.state = {
            list:[]
        }
    }

    componentDidMount(){
        var _this = this;
        $.ajax({
            type: "get",
            url: "https://vipshop.herokuapp.com/api/v1/product/byproducttype",
            data: {
                per:8,
                page:1,
                ptname:"鞋包配饰"
            },
            dataType: "json",
            success: function (data) {
                _this.setState({list:data.info.products})
            }
        });
    }

    tap(id){
        this.props.history.push({pathname:"/detail/" + id})
        
      }

    render(){
        var _this = this;
        return (
            <div>
                <div className="fenlei">
                    <figure>
                        <figcaption><img src="https://img10.360buyimg.com/n2/jfs/t1/10348/8/10003/93153/5c4c8480E0d867ebc/30766aac1134e99a.jpg"/></figcaption>
                        <span>潮流女鞋</span>
                    </figure>
                    <figure>
                        <figcaption><img src="https://img13.360buyimg.com/cms/jfs/t18820/357/554273581/38764/c6c448e9/5a9698fcN20fb63bc.jpg"/></figcaption>
                        <span>潮流男鞋</span>
                    </figure>
                    <figure>
                        <figcaption><img src="https://img10.360buyimg.com/jdcms/jfs/t30022/302/1627655540/43128/1f1669a5/5ce66d71Ncd20c247.jpeg"/></figcaption>
                        <span>时尚女包</span>
                    </figure>
                    <figure>
                        <figcaption><img src="https://img11.360buyimg.com/jdcms/jfs/t1/22024/28/2005/8566/5c1867b4Eb6762081/56707fdc5d5270d6.jpeg"/></figcaption>
                        <span>男包箱包</span>
                    </figure>
                </div>

                <div className="dress-list">
                    {   this.state.list.map((item,i) => {
                        return (
                            <div className="dress-item" key={i} onClick={_this.tap.bind(_this,item._id)}>
                                <img src={item.imgs[0]}/>
                                <p>{item.description}</p>
                                <span>￥{item.style[0].price}</span>
                                <button>去拼团</button>
                            </div>
                        )
                    })
                        
                    }
                </div>
            </div>
        )
    }
}

export default Shoes