import React,{Component} from 'react';
import $ from "jquery"
import '../css/Dress.css';

class Department extends Component{
    constructor(props){
        super(props)
        this.state = {
            list:[]
        }
    }

    componentDidMount(){
        var _this = this;
        $.ajax({
            type: "get",
            url: "https://vipshop.herokuapp.com/api/v1/product/byproducttype",
            data: {
                per:8,
                page:1,
                ptname:"母婴用品"
            },
            dataType: "json",
            success: function (data) {
                _this.setState({list:data.info.products})
            }
        });
    }

    tap(id){
        this.props.history.push({pathname:"/detail/" + id})
        
      }

    render(){
        var _this = this;
        return (
            <div>
                <div className="fenlei">
                    <figure>
                        <figcaption><img src="https://img13.360buyimg.com/n2/jfs/t2953/167/179422502/212640/b28fdf6f/574e8877Nc1291ed6.jpg!q80.webp"/></figcaption>
                        <span>玩具乐器</span>
                    </figure>
                    <figure>
                        <figcaption><img src="https://img11.360buyimg.com/cms/jfs/t6934/90/1742519078/26508/d3f4d191/59845c2dNcc08980a.jpg"/></figcaption>
                        <span>母婴用品</span>
                    </figure>
                    <figure>
                        <figcaption><img src="https://img12.360buyimg.com/cms/jfs/t14008/63/718523200/44181/8dba3366/5a12c388N5d2f6843.jpg"/></figcaption>
                        <span>童装童鞋</span>
                    </figure>
                    <figure>
                        <figcaption><img src="https://img13.360buyimg.com/n2/jfs/t23713/131/2052845965/177971/5c3c184e/5b714823Naf34a75b.jpg!q80.webp"/></figcaption>
                        <span>妈妈专区</span>
                    </figure>
                </div>

                <div className="dress-list">
                    {   this.state.list.map((item,i) => {
                        return (
                            <div className="dress-item" key={i} onClick={_this.tap.bind(_this,item._id)}>
                                <img src={item.imgs[0]}/>
                                <p>{item.description}</p>
                                <span>￥{item.style[0].price}</span>
                                <button>去拼团</button>
                            </div>
                        )
                    })
                        
                    }
                </div>
            </div>
        )
    }
}

export default Department