import React,{Component} from "react"
import { Carousel,WingBlank,ActionSheet, WhiteSpace, Button, Toast,Icon } from 'antd-mobile';
import "../css/Detail.css"
import $ from "jquery"

class Detail extends Component{
    constructor(props){
        super(props)
        this.state = {
            id:this.props.match.params.id?this.props.match.params.id : "",
            list:[],
            list1:[],
            style:"",
        }
    }

    dataList = [
        { url: 'OpHiXAcYzmPQHcdlLFrc', title: '发送给朋友' },
        { url: 'wvEzCMiDZjthhAOcwTOu', title: '新浪微博' },
        { url: 'cTTayShKtEIdQVEMuiWt', title: '生活圈' },
        { url: 'umnHwvEgSyQtXlZjNJTt', title: '微信好友' },
        { url: 'SxpunpETIwdxNjcJamwB', title: 'QQ' },
      ].map(obj => ({
        icon: <img src={`https://gw.alipayobjects.com/zos/rmsportal/${obj.url}.png`} alt={obj.title} style={{ width: 36 }} />,
        title: obj.title,
      }));

      showShareActionSheet = () => {
        ActionSheet.showShareActionSheetWithOptions({
          options: this.dataList,
          // title: 'title',
          message: '爱分享的人运气都不会差！',
        },
        (buttonIndex) => {
          this.setState({ clicked1: buttonIndex > -1 ? this.dataList[buttonIndex].title : 'cancel' });
          // also support Promise
          return new Promise((resolve) => {
            Toast.info('取消分享');
            setTimeout(resolve, 100);
          });
        });
      }


    tap(){
        window.history.go(-1)
    }

    tap1(){
        this.props.history.push({pathname:"/confirm/" + this.state.id})
        
    }

    componentDidMount(){
        var _this = this
        $.ajax({
            type: "get",
            url: "https://vipshop.herokuapp.com/api/v1/product/" + this.state.id,
            dataType: "json",
            success: function (data) {
                _this.setState({list:data.info})
                _this.setState({list1:data.info.imgs})
                _this.setState({style:data.info.style[0].price})
            }
        });
    }

    render(){
        return (
            <div>
                <div className="banner">
                    <WingBlank>
                        <Carousel
                        className="detailcarousel"
                        autoplay={false}
                        infinite
                        >
                        {this.state.list1.map((val,i) => (
                            <a
                            key={i}
                            style={{ display: 'inline-block', width: '100%', height: "3rem" }}
                            >
                            <img
                                src={val}
                                alt=""
                                className="detailimg"
                                style={{ width: '100%',height:"3rem"}}
                                onLoad={() => {
                                // fire window resize event to change height
                                window.dispatchEvent(new Event('resize'));
                                this.setState({ imgHeight: 'auto' });
                                }}
                            />
                            </a>
                        ))}
                        </Carousel>
                    </WingBlank>
                    <span onClick={this.tap.bind(this)}><Icon type="left" size="lg"/></span>
                    <span>
                    <WingBlank>
                        <Button onClick={this.showShareActionSheet} style={{ width: "0.4rem",height:"0.4rem",background:"transparent",border:"none"}}><Icon type="ellipsis" /></Button>
                    </WingBlank>
                    </span>
                </div>
                <div className="info">
                    <span>￥{this.state.style}</span>
                </div>
                <p className="detaildesc">{this.state.list.description}</p>
                

                <div className="yunfei">
                    运费：在线支付免运费
                </div>

                <div>
                    {
                        this.state.list1.map(function(item,i){
                            return (
                                <img src={item} key={i} style={{ width: '100%',height:"3rem"}}/>
                            )
                        })  
                    }
                </div>

                <div className="nowbuy" onClick={this.tap1.bind(this)}>
                    <button>立即购买</button>
                </div>
            </div>
        )
    }
}

export default Detail