import React,{Component} from "react";
import $ from 'jquery';
import "../css/Register.css";
import Header from "./Header";

class Register extends Component{   
    constructor(props){
        super(props)
        this.state={
            headname:"注册账号",
            data:{}
        }
    }
    reg(){
        var _this=this;
        var name=this.refs.txt1.value;
        var tel=this.refs.txt2.value;
        var psw=this.refs.txt3.value;
        if(!name){
            alert("用户名不能为空")
        }else if(!tel){
            alert("手机号不能为空")
        }else if(!/^1[345678]\d{9}$/.test(tel)){
            alert("手机号格式错误")
        }else if(!/^[a-zA-Z_]\w{5,14}$/.test(psw)){
            alert("密码必须以字母开头，6-16位")
        }else{
            $.ajax({
                type:"post",
                url:"https://vipshop.herokuapp.com/api/v1/auth/user_reg",
                data:{
                    username: name,
                    password: psw,
                    coverImg:"https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=234634259,4236876085&fm=27&gp=0.jpg"
                },
                async:true,
                dataType:'json',
                success:function(data){
                    _this.setState({data:data})
                    console.log(data)
                    if(data.status=="success"){
                        alert("恭喜你注册成功");
                        window.location.href = '#/login'
                    }
                }
            });
        }        
    }
    tologin(){
        window.location.href = '#/login'
    }
    render(){       
        return(
            <div className="register">
                <Header name={this.state.headname}/>
                <div className="ipt">
                    <div><span>用户名：</span><input type="text" placeholder="请输入用户名" ref="txt1"/></div>
                    <div><span>手机号：</span><input type="number" placeholder="请输入手机号" ref="txt2"/></div>
                    <div><span>密码：</span><input type="password" placeholder="请输入密码" ref="txt3"/></div>
                </div>
                <div className="agree">注册即代表你同意<span>十元街隐私政策</span></div>
                <p className="reg" onClick={this.reg.bind(this)}>注册</p>
                <p className="reg1" onClick={this.tologin.bind(this)}>已有账号，去登录</p>
            </div>
        )
        
    }
}

export default Register