import React,{Component} from "react"
import { Icon, Grid } from 'antd-mobile';
import {HashRouter as Router,Route,NavLink,Redirect,Switch} from "react-router-dom"
import Souye from "../views/Souye"
import Person1 from "../views/Person1"
import Person from "../views/Person"
import "../css/Index.css"
import "../font/iconfont.css"

class Index extends Component{
    tap(){
        if(sessionStorage.getItem("token")){
            this.props.history.push('/index/person')
        }else{
            this.props.history.push('/index/person1')
        }
    }
    render(){
        return (
            <div>
                <Router>
                    <div>
                    <div className="footer">
                        <div>
                            <NavLink to="/index/souye">
                                <figure>
                                    <figcaption><span className="iconfont icon-index"></span></figcaption>
                                    <span>首页</span>
                                </figure>
                            </NavLink>
                                <figure onClick={this.tap.bind(this)}>
                                    <figcaption><span className="iconfont icon-renwu"></span></figcaption>
                                    <span>个人中心</span>
                                </figure>
                        </div>
                    </div>
                    <Switch>
                        <Route path="/index/souye" component={Souye}/>
                        <Route path="/index/person1" component={Person1}/>
                        <Route path="/index/person" component={Person}/>
                    </Switch>
                    </div>
                </Router>
            </div>
        )
    }
}

export default Index