import React,{Component} from 'react';
import $ from "jquery"
import '../css/Dress.css';

class Wash extends Component{
    constructor(props){
        super(props)
        this.state = {
            list:[]
        }
    }

    componentDidMount(){
        var _this = this;
        $.ajax({
            type: "get",
            url: "https://vipshop.herokuapp.com/api/v1/product/byproducttype",
            data: {
                per:8,
                page:1,
                ptname:"洗护"
            },
            dataType: "json",
            success: function (data) {
                _this.setState({list:data.info.products})
            }
        });
    }

    tap(id){
        this.props.history.push({pathname:"/detail/" + id})
        
      }

    render(){
        var _this = this;
        return (
            <div>
                <div className="fenlei">
                    <figure>
                        <figcaption><img src="https://img13.360buyimg.com/n2/jfs/t1/11553/11/2892/130736/5c18aa72Ea10b21c3/ea747c3e49fcbced.jpg"/></figcaption>
                        <span>面部护肤</span>
                    </figure>
                    <figure>
                        <figcaption><img src="https://img14.360buyimg.com/n2/jfs/t1/5702/20/8423/208947/5ba9a1ccE8df343c8/52e44400f498651b.jpg"/></figcaption>
                        <span>个人护理</span>
                    </figure>
                    <figure>
                        <figcaption><img src="https://img11.360buyimg.com/n2/jfs/t1/10721/32/897/485590/5bdc12c3E7302f088/8946557527ee6cd7.jpg"/></figcaption>
                        <span>香水彩妆</span>
                    </figure>
                    <figure>
                        <figcaption><img src="https://img13.360buyimg.com/n2/jfs/t1/56731/7/12/109087/5cd17782E794d1d1c/47116d4d9660a719.jpg"/></figcaption>
                        <span>洗发护发</span>
                    </figure>
                </div>

                <div className="dress-list">
                    {   this.state.list.map((item,i) => {
                        return (
                            <div className="dress-item" key={i} onClick={_this.tap.bind(_this,item._id)}>
                                <img src={item.imgs[0]}/>
                                <p>{item.description}</p>
                                <span>￥{item.style[0].price}</span>
                                <button>去拼团</button>
                            </div>
                        )
                    })
                        
                    }
                </div>
            </div>
        )
    }
}

export default Wash