import React,{Component} from "react";
import {HashRouter as Router,Route,NavLink,Redirect,Switch} from "react-router-dom"
import "../css/Login.css";
import "../css/Register.css";
import Header from "./Header";
import $ from 'jquery';

class Register extends Component{   
    constructor(props){
        super(props)
        this.state={
            headname:"账号登录"
        }
    }
    login(){
        
        var name=this.refs.txt1.value;
        var psw=this.refs.txt2.value;
        var _this = this
        if(!name){
            alert("用户名不能为空")
        }else if(!/^[a-zA-Z_]\w{5,14}$/.test(psw)){
            alert("密码必须以字母开头，6-16位")
        }else{
            $.ajax({
                type:"post",
                url:"https://vipshop.herokuapp.com/api/v1/auth/user_login",
                data:{
                    username: name,
                    password: psw,
                },
                async:true,
                dataType:'json',
                success:function(data){
                   // console.log(data)
                    if(data.status=="success"){
                        alert("恭喜你登录成功");
                        sessionStorage.setItem("token", data.info.token)
                        sessionStorage.setItem("name", data.info.username)
                        _this.props.history.push('/index/person')
                    }else{
                        alert("用户名或密码错误")
                    }
                }
            })
        }
        
    }
    toregister(){
        this.props.history.push('/register')
    }
    render(){       
        return(
            <div className="register">
                <Header name={this.state.headname}/>
                <div className="ipt">
                    <div><span>用户名：</span><input type="text" placeholder="请输入用户名" ref="txt1"/></div>
                    
                    <div><span>密码：</span><input type="password" placeholder="请输入密码" ref="txt2"/></div>
                </div>
                <div className="agree">登录即代表你同意<span>十元街隐私政策</span></div>
                <p className="reg" onClick={this.login.bind(this)}>登录</p>
                <p className="reg1" onClick={this.toregister.bind(this)}>没有账号？去注册</p>
            </div>
        )
    }
}

export default Register